import { build } from './builder'
import { download } from './common'

const {
  search: searchLeases,
  get: getLeases,
  post: postLease,
  put: putLease,
  delete: deleteLease
} = build('/rent', 'leaseId')

async function getLeaseDashboard () {
  const { data } = await download({
    method: 'get',
    url: `/rent/dashboard`
  })
  return data || []
}

export {
  searchLeases, getLeases, postLease, putLease, deleteLease,
  getLeaseDashboard
}
